/* Header
------------------------------------------------------------*/
.c-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 20px 0;
  position: relative;
  background: rgba(255, 255, 255, 0.9);
}

.c-header .bder {
  border-bottom: 1px solid #a8a8a8;
  padding-bottom: 20px;
}

/* Logo
------------------------------------------------------------*/
/* Main menu
------------------------------------------------------------*/
.c-gnav li {
  display: inline-block;
}

.c-gnav li + li {
  margin-left: 20px;
}

.c-gnav a {
  font-size: 1.6rem;
  text-transform: capitalize;
}

.c-gnav a:focus {
  color: red;
}

/*------------------------------------------------------------
footer
------------------------------------------------------------*/
.c-footer {
  padding: 20px 20px;
  color: #000;
}

.c-footer .box {
  display: flex;
  justify-content: space-between;
}

.c-footer .box__link {
  width: 30%;
}

.c-footer .box__link:nth-of-type(2) ul li::before {
  background: url("../img/ic_ft_2.png") left center no-repeat;
  width: 5px;
  height: 7px;
}

.c-footer .box__link:nth-of-type(3) ul li {
  padding-left: 20px;
}

.c-footer .box__link:nth-of-type(3) ul li::before {
  background: url("../img/ic_ft_3.png") left center no-repeat;
  width: 11px;
  height: 11px;
}

.c-footer .box__link h3 {
  border-bottom: 1px solid #cccccc;
  font-weight: bold;
  padding-bottom: 5px;
  margin-bottom: 5px;
}

.c-footer .box__link ul li {
  position: relative;
  padding-left: 15px;
}

.c-footer .box__link ul li::before {
  position: absolute;
  content: "";
  background: url("../img/ic_ft_1.png") left center no-repeat;
  background-size: 100%;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 5px;
  height: 6px;
}

.c-footer .copy_r {
  border-top: 1px solid #e3e3e3;
  margin-top: 25px;
  padding-top: 20px;
  font-size: 1.2rem;
  text-align: center;
  font-weight: 500;
}

