/* Breadcrumb
------------------------------------------------------------*/
.c-breadcrumb__bor {
  border-top: 1px solid #cccccc;
  border-bottom: 1px solid #cccccc;
  margin-bottom: 15px;
}

.c-breadcrumb {
  margin: 10px 0 30px 0;
  padding: 5px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.c-breadcrumb--m00 {
  margin: 0;
}

.c-breadcrumb li:first-of-type::before {
  content: "";
  margin: 0;
  padding-left: 0;
}

.c-breadcrumb li:first-of-type a:active {
  text-decoration: underline;
}

.c-breadcrumb li::before {
  content: "";
  margin: 0 10px;
  padding-left: 6px;
  color: #000000;
  font-size: 14px;
  background: url("../img/ic_ft_2.png") right center no-repeat;
  background-size: 100%;
  width: 5px;
  height: 7px;
}

.c-breadcrumb li a {
  font-size: 1.4rem;
  font-weight: 500;
}

.c-breadcrumb li a img {
  vertical-align: middle;
  margin-top: -5px;
}

